import {Load} from "../../../../../../common/react/load";
import {unique, sort, uniqueDeep} from "../../../../../../common/utils/collections";
import {cs} from "../../../../../../common/react/chain-services";
import * as React from "react";
import {LsState} from "../../../../../../common/react/ls-state";
import "../common/main.scss";
import {Layout} from "../../common/layout/layout";

export const MatchPlus = ({}) => cs(
    ({}, next) => Layout({next}),
    ["config", (_, next) => LsState({
        key: "math-1a.config",
        defaultValue: {
            max: 50,
        },
        next,
    })],

    ["result", ({config}, next) => Load({
        _key: JSON.stringify(config.value),
        fetch: () => generate(config.value),
        next,
    })],
    ({result}) => result && (
        <div className="math-main-1">
            <div className="">
                Biểu diễn các số dưới đây dưới dạng tổng của 2 số cho trước
            </div>
            <div className="">
                {result.feeds.join(", ")}
            </div>
            <div className="">
                {result.combis.map(({i1, i2}, i) => (
                    <div key={i}>
                        {result.feeds[i1] + result.feeds[i2]} = {result.feeds[i1]} + {result.feeds[i2]}
                    </div>
                ))}
            </div>
        </div>
    )
);

const generate = ({max}) => {
    const feeds = (() => {
        let nums = [];
        for (;nums.length < 10;) {
            nums.push(Math.floor(Math.random() * 30) + 1);
            nums = unique(nums);
        }
        return sort(nums);
    })();

    const combis = (() => {
        let combis = [];

        for (;combis.length < 10;) {
            const combi = (() => {
                const i1 = randomFrom(0, feeds.length - 1);
                const i2 = randomFrom(i1 + 1, feeds.length);
                return {i1, i2};
            })();
            if (feeds[combi.i1] + feeds[combi.i2] <= max) {
                combis.push(combi);
                combis = uniqueDeep(combis);
            }
        }
        return sort(combis, ({i1, i2}) => i1 + i2);
    })();

    // console.log(feeds)
    return {feeds, combis};
};

const randomFrom = (from, to) => {
    return Math.floor(Math.random() * (to - from)) + from;
};